<template>
  <div id="app" class="app">
    <page :currentPage="currentPage">
      <h1 class="text-center">自我介绍</h1>
      <section class="animate" ref="section1">
        <p class="demo-intro">花名：香帅</p>
        <p class="demo-intro">get：angular, vue, node, express, koa</p>
        <p class="demo-intro"></p>
      </section>
    </page>
    <page :currentPage="currentPage">
      <h1 class="text-center">项目说明</h1>
      <section class="animate move-left">
        <p>最近负责市风险管理系统</p>

        <dl>
          <dt>简介:</dt>
          <dd>
            <ul>
              <li>
                <span class="text-bold">目的:</span> 加强突发事件风险管理，是服务全市经济社会发展全局、确保人民群众生命财产安全的重要手段，是在应急管理新常态下做好突发事件预防和应急准备工作的重要抓手，是各级政府及部门坚持以人为本、执政为民、全面履行政府职能的重要体现</li>
            </ul>
          </dd>
        </dl>
      </section>
    </page>
    <page :currentPage="currentPage">
      <h1 class="text-center">个人爱好</h1>
      <section class="animate move-left">
        <p>运动</p>
        <ul>
          <li>篮球，乒乓球，台球</li>
        </ul>
        <p>唱歌</p>
          <ul>
            <li>主要是一些老歌，小清新的歌曲不太会</li>
          </ul>
      </section>
    </page>
    <page :currentPage="currentPage">
      <h1 class="text-center">联系我吧</h1>
      <section class="animate move-left">
        <img class="avatar" src="./assets/avatar.jpg" alt="头像" style="width:200px;">
        <div class="author-info">
          <p>我这里只是简单的做了一下自我介绍，
            <ul>
              <li>QQ: 709541626</li>
              <li>微信: e50jaywe</li>
              <li>邮箱: e50jaywe@163.com</li>
              <li>githup: e50jaywe</li>
            </ul>
          </p>
        </div>
      </section>
    </page>
    <page-controller :pageNum="pageNum" :currentPage="currentPage" @changePage="changePage" :option="controllerOption"></page-controller>
  </div>
</template>

<script>
import Page from './components/Page.vue';
import PageController from './components/PageController.vue';

// 页面进出动画
function afterEnterAnimate($child) {
  $child.$el.querySelector('.animate').classList.remove('move-left', 'move-right');
}
function beforeLeaveAnimate($child) {
  let moveType = Math.random() > 0.5 ? 'move-left' : 'move-right';
  $child.$el.querySelector('.animate').classList.add(moveType);
}

export default {
  name: 'app',
  data() {
    return {
      currentPage: 1,
      options: [{
        // the color of background
        background: 'rgba(229, 199, 46, 1)',
        // the color of text
        color: '#fff',
        // is content center
        isCenter: true,
        // the function before page show
        afterEnter: afterEnterAnimate,
        // the function after page show
        beforeLeave: beforeLeaveAnimate
      }, {
        background: 'rgba(79, 204, 76, 1)',
        color: '#fff',
        isCenter: true,
        afterEnter: afterEnterAnimate,
        beforeLeave: beforeLeaveAnimate
      }, {
        background: 'rgba(233, 84, 84, 1)',
        color: '#fff',
        isCenter: true,
        afterEnter: afterEnterAnimate,
        beforeLeave: beforeLeaveAnimate
      }, {
        background: 'rgba(46, 153, 229, 1)',
        color: '#fff',
        isCenter: true,
        afterEnter: afterEnterAnimate,
        beforeLeave: beforeLeaveAnimate
      }],
      controllerOption: {
        arrowsType: false,
        navbar: true,
        highlight: true,
        loop: false
      }
    }
  },
  computed: {
    // 总page数
    pageNum() {
      return this.options.length;
    }
  },
  methods: {
    changePage(index) {
      // beforeLeave Hook
      let beforeIndex = this.currentPage - 1;
      let leaveFunction = this.options[beforeIndex].beforeLeave;
      typeof leaveFunction === 'function' && leaveFunction.call(this, this.$children[beforeIndex]);
      // change page
      this.currentPage = index;
      // afterEnter Hook
      let nextIndex = index - 1;
      let enterFunction = this.options[nextIndex].afterEnter;
      this.$nextTick(function() {
        typeof enterFunction === 'function' && enterFunction.call(this, this.$children[nextIndex]);
      })
    }
  },
  components: {
    Page, PageController
  },
  mounted() {
    this.$children.forEach((child, index) => {
      // 动态设置各个page内的options
      if (child.option === null) {
        let childOption = this.options[index];
        this.$set(childOption, 'index', index + 1);
        child.option = childOption;
      }
    });
  }
}
</script>

<style>
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
}

.app {
  height: 100%;
  width: 100%;
}



/* 下面的是与fullPage无关的样式 */

.animate {
  transition: all 1s ease-out 0s;
}

.move-left {
  transform: translateX(-1000%);
}

.move-right {
  transform: translateX(1000%);
}

@media screen and (max-width:768px) {
  html,
  body {
    font-size: 12px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: underline;
}

.person-img {
  width: 223px;
  height: 185px;
  float: left;
  background-color: #fff;
  box-shadow: 3px 3px 10px #999;
}

.person-img img {
  height: 100%;
  width: 100%;
}

.person-basic-info {
  overflow: hidden;
  float: right;
  margin-left: 40px;
}

.info-line {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.text-center {
  text-align: center;
}

.text-bold {
  font-weight: bold;
}

.demo-intro {
  text-indent: 2em;
}

dt {
  font-weight: bold;
  font-size: 16px;
}

ul {
  padding-left: 1em;
}

.avatar {
  margin: 10px auto;
  display: block;
  box-shadow: 1px 1px 5px #666;
}

.author-info {
  text-align: center;
}
</style>



